<template>
  <div clas="parent">
    <!--  -->
    <div class="f1">
      <h4></h4>
      <div class="admin_parent">
        <div class="admin_son">
          <div class="first">
            <label>:</label>
            <span v-if="show">{{gname}}</span>
            <input v-model="gname" v-else type="text" />
            <a href="javascript:;" @click="showCount">{{msg}}</a>
          </div>
          <div class="two">
            <label>:</label>
            <span v-if="word">{{gphone}}</span>
            <input v-model="gphone" v-else type="text" />
            <a href="javascript:;" @click="showBtn()">{{showMsg}}</a>
          </div>
          <div class="three">
            <label>:</label>
            <span v-if="three">{{gemail}}</span>
            <input v-model="gemail" v-else type="text" />
            <a href="javascrip:;" @click="show_can()">{{canMsg}}</a>
          </div>
          <div class="jifen">
            <label>:</label>
            <span>{{gpoint}}&nbsp;</span>
          </div>
        </div>
      </div>
    </div>
    <!--  -->
    <div class="f2">
      <div class="two_top">
        <h4 class="title"></h4>
        <span></span>
        <a href="javascript:;" @click="show_title()">{{realmsg}}</a>
      </div>
      <div class="two_middle">
        <label>:</label>
        <span v-if="two_name">{{guserName}}</span>
        <input v-model="guserName" v-else placeholder="" type="text" />
      </div>
      <div class="two_midd">
        <label>:</label>
        <span v-if="two_name">{{gcard}}</span>
        <input v-model="gcard" v-else placeholder="" type="text" />
      </div>
    </div>
    <Alert :display="display" :prompt="prompt"></Alert>
  </div>
</template>
<script>
import Alert from "../Alert";
export default {
  components: { Alert },
  data() {
    return {
      display: "none",
      prompt: "",
      d1: 0,
      show: true,
      word: true,
      three: true,
      two_name: true,
      showMsg: "",
      msg: "",
      canMsg: "",
      realmsg: "",
      gname: "",
      gphone: "",
      gemail: "",
      guserName: "",
      gcard: "",
      gpoint: ""
    };
  },
  methods: {
    /*    */

    load() {
      var url = "/user/admin_to";
      this.axios
        .get(url, { params: { id: sessionStorage.getItem("userid") } })
        .then(res => {
          this.gname = res.data.result[0].UserName;
          this.gphone = res.data.result[0].phone;
          this.guserName = res.data.result[0].realName;
          this.gcard = res.data.result[0].cardID;
          this.gemail = res.data.result[0].email;
          this.gpoint = res.data.result[0].point;
        });
    },
    // 
    sendRequest(obj) {
      var url = "user/admin_up";
      obj.id = sessionStorage.getItem("userid");
      this.axios.post(url, obj).then(res => {
        if (res.data.code == 1) {
          console.log("");
        }
        if (res.data.code == -1) {
          console.log("");
        }
      });
    },
    /*x */
    showCount() {
      if (this.show == true) {
        this.show = false;
        this.msg = "";
      } else {
        if (this.msg == "") {
          var reg = /^\w{3,8}$/;
          if (reg.test(this.gname) == false) {
            // console.log("hhh");
            this.gname = "";
            this.prompt = "";
            this.display = "block" + this.d1++;
          }else{
            var obj = {
              UserName: this.gname
            };
            this.sendRequest(obj);
          }
          this.show = true;
          this.msg = "";
        }
      }
    },
    /* two  */
    showBtn() {
      if (this.word == true) {
        this.word = false;
        this.showMsg = "";
      } else {
        if (this.showMsg == "") {
          var reg = /^1[3-9]\d{9}$/;
          if (reg.test(this.gphone) == false) {
            this.gphone = "";
            this.prompt = "";
            this.display = "block" + this.d1++;
          }else{
            var obj = {
              phone: this.gphone
            };
            this.sendRequest(obj);
          }
          this.word = true;
          this.showMsg = "";
        }
      }
    },
    /*three  // */
    show_can() {
      if (this.three == true) {
        this.three = false;
        this.canMsg = "";
      } else {
        if (this.canMsg == "") {
          var reg = /^[0-9a-z_]+@(([0-9a-z]+)[.]){1,2}[a-z]{2,3}$/;
          if (reg.test(this.gemail) == false) {
            this.gemail = "";
            this.prompt = "";
            this.display = "block" + this.d1++;
          }else{
            var obj = {
              email: this.gemail
            };
            this.sendRequest(obj);
          }
          this.three = true;
          this.canMsg = "";
        }    
      }
    },
    // 
    show_title() {
      // this.show_name=!this.show_name;
      if (this.two_name == true) {
        this.two_name = false;
        this.realmsg = "";
      } else {
        if (this.realmsg == "") {
          var reg = /^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/;
          if (reg.test(this.gcard) == false) {
            this.gcard = "";
            this.prompt = "";
            this.display = "block" + this.d1++;
          }else{
            var obj = {
              realName: this.guserName,
              cardID: this.gcard
            };
            this.sendRequest(obj);
          }
          this.realmsg = "";
          this.two_name = true;
        }    
      }
    }
  },
  created() {
    this.load();
    //this.showCount();
  }
}; //export end
</script>
