            <div class="content">
              <div class="pic_wrapper">
                <img
                  width="210"
                  height="150"
                  :src="`http://127.0.0.1:3003/${item.ImgUrl}`"
                  alt
                  class="pic"
                />
              </div>
              <div class="side_info">
                <div class="title">
                  :{{item.houseTitle}}
                  <a href="javascript:;">{{item.collectime}}</a>
                </div>
                <div class="desc">
                  <table>
                    <tr>
                      <td>
                        <span>{{item.parentName}}{{item.aTypeName}}</span>
                      </td>
                      <td>
                        <span>{{item.address}}</span>
                      </td>
                    </tr>
                    <tr>
                      <td>
                        <span>{{item.roomSize.toFixed(2)}}</span>
                      </td>
                      <td>
                        <span>{{item.bedroom}}{{item.saloon}}{{item.toilet}}{{item.kitchen}}{{item.balcony}}</span>
                      </td>
                    </tr>
                    <tr>
                      <td>
                        <span>{{item.normalPrice.toFixed(2)}}</span>
                      </td>
                      <td>
                        <span>{{item.festivalPrice.toFixed(2)}}</span>
                      </td>
                    </tr>
                    <tr>
                      <td>
                        <span>{{item.cashMoney==""?"0.00":item.cashMoney.toFixed(2)}}</span>
                      </td>
                      <td>
                        <span>{{item.otherMoney}}</span>
                      </td>
                    </tr>
                    <tr>
                      <!-- <td>
                        <span>{{item.traffic}}</span>
                      </td> -->
                      <td colspan="2">
                        <span>{{item.phone}}</span>
                      </td>
                    </tr>
                    <!-- <tr>
                      <td colspan="2">
                        <span>{{item.houseDESC}}</span>
                      </td>
                      <td></td>
                    </tr> -->
                    <tr>
                      <td colspan="2">
                        <div style="float:left"></div>
                        <div style="float:left">
                          <div v-for="(item,i) of listBed" :key="i">
                          <input type="checkbox" disabled="false" checked />
                          {{item.bedType}}:
                          -{{item.width}} -{{item.extent}} -{{item.num}}:{{item.description}}<br>
                        </div>
                        </div>
                      </td>
                      <td></td>
                    </tr>
                    <tr>
                      <td colspan="2">
                        <span></span>
                        <span v-for="(item,i) of listinstallation" :key="i">
                          <input type="checkbox" disabled="false" checked />
                          {{item.installName}}
                        </span>
                      </td>
                      <td></td>
                    </tr>
                  </table>
                </div>
              </div>
            </div>
            <div class="handle">
              <button class="fa_btn" @click="delCollectByid(item.collectid)"></button>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div :style="{display:isnav}" class="pagenav">
      <v-pagination :total="total" :currentPage="current" @pagechange="pagechange"></v-pagination>
    </div>
  </div>
</template>
<script>
import pagination from "../page/page";
export default {
  data() {
    return {
      userid: sessionStorage.getItem("userid"), //
      isnav: "flex",
      total: 0, // 
      pageSize: 0, // 
      current: 1, // 
      pageCount: "", //
      list: [], //axios
      listinstallation: [], //axioshid
      listBed: [] //axioshid
    };
  },
  methods: {
    //
    delCollectByid(id){
      if (confirm("?")) {
        if (id != "" && id != "underfind") {
          var obj = { id };
          this.axios.post("admin/DelCollectByid", obj).then(res => {
            if (res.data.code == 1) {
              alert("");
              this.getList();
            } else {
              alert("");
            }
          });
        }
      }
    },
    pagechange(currentPage) {
      this.getList(currentPage);
      // ajax,  currentPage, 
    },
    getList(currentPage) {
      if (currentPage > 0) {
        currentPage = currentPage - 1;
      } else {
        currentPage = 0;
      }
      var obj = { id: this.userid, currentPage: currentPage };
      //  http://127.0.0.1:3003/admin/GetOrderMsglist
      this.axios.get("admin/GetCollectlist", { params: obj }).then(res => {
        this.list = res.data.data;
        this.total = res.data.count; // 
        this.pageSize = res.data.pageSize; // 
        this.pageCount = res.data.pageCount; //
        if (this.list.length > 0) {
          this.isnav = "flex";
          for (var i = 0; i < this.list.length; i++) {
            obj = { hid: this.list[i].id };
            this.axios
              .get("admin/GetInstallationByhid", { params: obj })
              .then(res => {
                this.listinstallation = res.data.data;
              });
            this.axios.get("admin/GetBedByhid", { params: obj }).then(res => {
              this.listBed = res.data.data;
            });
          }
        } else {
          this.isnav = "none";
        }
      });
    }
  },
  mounted() {
    this.getList();
  },
  components: {
    "v-pagination": pagination
  }
};
</script>
